#input-bar-container {
    width:100%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: #1C688E;
}

input[type="text"] {
    width:100%;
    margin:20px;
    border-radius: 3px;
    font-family: Arial, Helvetica, sans-serif;
    color:#D8D8D8;
    font-size: 1.1em;
    height:40px;
    font-weight: 100;
    border:none;
    padding:0px 10px;
}
input[placeholder], [placeholder], *[placeholder] {
    color: #888;
}

input[type="text"]:focus {
    border:none;
    outline:none;
    -webkit-box-shadow: inset 1px 1px 1px #ccc;
            box-shadow: inset 1px 1px 1px #ccc;
}




